'use client';

import { useState, useMemo, useEffect } from 'react';
import Button from '@/components/ui/Button';

const PRODUCTS = [
    {
        id: 1,
        name: "VALENCIA ORANGE MOCHA",
        price: "$7.50",
        category: "Signature",
        description: "A bold fusion of dark cocoa and zesty Valencia oranges, topped with crystalized zest.",
        image: "https://images.unsplash.com/photo-1541167760496-162955ed8a9f?q=80&w=2036&auto=format&fit=crop",
        tags: ["Zesty", "Dark Cocoa", "Hot"]
    },
    {
        id: 2,
        name: "KYOTO COLD DRIP",
        price: "$8.00",
        category: "Cold Brew",
        description: "Slow-dripped over 12 hours through glass towers to extract pure, floral clarity.",
        image: "https://images.unsplash.com/photo-1517701604599-bb29b565090c?q=80&w=1974&auto=format&fit=crop",
        tags: ["Floral", "Clear", "Drip"]
    },
    {
        id: 3,
        name: "BLACK GOLD ESPRESSO",
        price: "$4.50",
        category: "Strong",
        description: "Our darkest roast. Intense complexity with notes of smoke and black treacle.",
        image: "https://images.unsplash.com/photo-1514432324607-a09d9b4aefdd?q=80&w=1974&auto=format&fit=crop",
        tags: ["Intense", "Smoky", "Short"]
    },
    {
        id: 4,
        name: "VELVET FLAT WHITE",
        price: "$5.95",
        category: "Classics",
        description: "Micro-foamed milk poured over a precision-pulled double shot of our house blend.",
        image: "https://images.unsplash.com/photo-1534706936160-d5ee67737049?q=80&w=1974&auto=format&fit=crop",
        tags: ["Creamy", "Balanced", "Classic"]
    },
    {
        id: 5,
        name: "TURKISH ROSE LATTE",
        price: "$6.50",
        category: "Signature",
        description: "Delicate rose water infusion with crushed pistachios and honey-drizzled foam.",
        image: "https://images.unsplash.com/photo-1485808191679-5f86510681a2?q=80&w=1974&auto=format&fit=crop",
        tags: ["Floral", "Sweet", "Exotic"]
    },
    {
        id: 6,
        name: "HONEYCOMB NITRO",
        price: "$7.25",
        category: "Cold Brew",
        description: "Nitro-infused cold brew served with a shard of handmade honeycomb toffee.",
        image: "https://images.unsplash.com/photo-1572286258217-40142c1c6a70?q=80&w=2070&auto=format&fit=crop",
        tags: ["Creamy", "Sweet", "Draft"]
    }
];

const CATEGORIES = ["All", "Signature", "Classics", "Cold Brew", "Strong"];

export default function ProductsPage() {
    const [selectedCategory, setSelectedCategory] = useState("All");
    const [isLoaded, setIsLoaded] = useState(false);

    useEffect(() => {
        setIsLoaded(true);
    }, []);

    const filteredProducts = useMemo(() => {
        return selectedCategory === "All"
            ? PRODUCTS
            : PRODUCTS.filter(p => p.category === selectedCategory);
    }, [selectedCategory]);

    return (
        <div className="min-h-screen bg-[#0C0908] text-white overflow-x-hidden">
            {/* Background Decorative Text */}
            <div className="fixed inset-0 pointer-events-none opacity-[0.02] flex items-center justify-center select-none z-0">
                <h1 className="text-[30vw] font-display whitespace-nowrap leading-none rotate-90 md:rotate-0">
                    NEXEN.CAFE
                </h1>
            </div>

            <div className="relative z-10 flex flex-col lg:flex-row min-h-screen">

                {/* Fixed Left Sidebar / Category Nav on Desktop */}
                <aside className="lg:w-80 lg:h-screen lg:fixed left-0 top-0 pt-32 pb-12 px-10 flex flex-col justify-between border-r border-white/5 bg-[#0C0908]/80 backdrop-blur-xl z-20">
                    <div className="space-y-12">
                        <div className="space-y-4">
                            <span className="text-gold-500 text-[10px] font-bold tracking-[0.4em] uppercase">Selection</span>
                            <h2 className="font-display text-4xl leading-none">CURATED<br />ROASTS</h2>
                        </div>

                        <nav className="flex flex-row lg:flex-col gap-6 overflow-x-auto pb-4 lg:pb-0 lg:overflow-visible">
                            {CATEGORIES.map((cat) => (
                                <button
                                    key={cat}
                                    onClick={() => setSelectedCategory(cat)}
                                    className={`text-left group flex items-center gap-4 transition-all duration-500 shrink-0 ${selectedCategory === cat ? 'lg:translate-x-4' : 'lg:hover:translate-x-2'}`}
                                >
                                    <span className={`hidden lg:block h-[1px] transition-all duration-500 bg-gold-500 ${selectedCategory === cat ? 'w-12' : 'w-0 group-hover:w-8'}`}></span>
                                    <span className={`text-xs font-bold tracking-widest uppercase transition-colors ${selectedCategory === cat ? 'text-gold-500' : 'text-zinc-500 group-hover:text-white'}`}>
                                        {cat}
                                    </span>
                                </button>
                            ))}
                        </nav>
                    </div>

                    <div className="hidden lg:block space-y-4 text-xs text-zinc-600 tracking-widest font-bold">
                        <p>Â© 2026 NEXEN MEDIA</p>
                        <p>EST. IN AUCKLAND</p>
                    </div>
                </aside>

                {/* Main Content Area */}
                <main className="lg:ml-80 flex-1 pt-32 lg:pt-20 px-6 md:px-12 lg:px-20 pb-40">

                    {/* Unique Header Section */}
                    <div className="mb-20 space-y-8 max-w-4xl">
                        <div className="flex items-center gap-4">
                            <span className="h-[2px] w-20 bg-gold-500"></span>
                            <span className="text-gold-500 text-[10px] font-bold tracking-[0.4em] uppercase">The Collection</span>
                        </div>
                        <h1 className="font-display text-7xl md:text-9xl tracking-tighter leading-[0.85] uppercase">
                            CRAFTING <br />
                            <span className="text-gold-500">LIQUID</span> GOLD
                        </h1>
                        <p className="text-zinc-400 text-lg md:text-xl max-w-2xl leading-relaxed italic border-l border-zinc-800 pl-8">
                            "Every bean is a universe. Every roast is a revelation. Welcome to the pinnacle of New Zealand's coffee culture."
                        </p>
                    </div>

                    {/* Dynamic Asymmetric Grid */}
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-12 lg:gap-20">
                        {filteredProducts.map((product, index) => (
                            <div
                                key={product.id}
                                className={`group relative space-y-6 transition-all duration-1000 ${isLoaded ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-20'}`}
                                style={{ transitionDelay: `${index * 150}ms` }}
                            >
                                <div className="relative aspect-[4/5] overflow-hidden rounded-sm bg-zinc-900 border border-white/5">
                                    <img
                                        src={product.image}
                                        alt={product.name}
                                        className="w-full h-full object-cover transition-transform duration-[3s] ease-out group-hover:scale-110 grayscale group-hover:grayscale-0"
                                    />
                                    <div className="absolute inset-0 bg-black/40 opacity-40 group-hover:opacity-10 transition-opacity duration-700"></div>

                                    {/* Numbering UI */}
                                    <span className="absolute top-8 left-8 font-display text-4xl text-white/10 group-hover:text-gold-500/50 transition-colors duration-700">
                                        0{product.id}
                                    </span>

                                    {/* Quick Actions */}
                                    <div className="absolute bottom-8 right-8 translate-y-12 opacity-0 group-hover:translate-y-0 group-hover:opacity-100 transition-all duration-700 z-10">
                                        <button className="bg-white text-black px-8 py-3 font-bold text-[10px] tracking-widest uppercase hover:bg-gold-500 hover:text-white transition-colors">
                                            Quick View
                                        </button>
                                    </div>
                                </div>

                                <div className="space-y-4 lg:pr-10">
                                    <div className="flex justify-between items-end gap-4">
                                        <h3 className="font-display text-4xl tracking-tight leading-none group-hover:text-gold-500 transition-colors duration-500 uppercase">
                                            {product.name}
                                        </h3>
                                        <span className="text-xl font-bold text-white tracking-widest shrink-0">{product.price}</span>
                                    </div>

                                    <div className="flex flex-wrap gap-2">
                                        {product.tags.map(tag => (
                                            <span key={tag} className="text-[9px] font-bold tracking-[0.2em] border border-zinc-800 px-3 py-1 rounded-full text-zinc-500 group-hover:border-gold-500/30 group-hover:text-gold-500 transition-all duration-700">
                                                {tag.toUpperCase()}
                                            </span>
                                        ))}
                                    </div>

                                    <p className="text-zinc-500 text-sm leading-relaxed max-w-sm">
                                        {product.description}
                                    </p>
                                </div>
                            </div>
                        ))}
                    </div>

                    {/* Signature Callout Section */}
                    <div className="mt-40 bg-[#120F0E] p-12 md:p-24 relative overflow-hidden flex flex-col items-center text-center space-y-8 border border-white/5 rounded-sm">
                        <div className="absolute top-0 right-10 leading-none font-display text-[200px] text-white/[0.02] select-none pointer-events-none">GOLD</div>
                        <h2 className="text-gold-500 text-xs font-bold tracking-[0.5em] uppercase">Artisan Quality</h2>
                        <h3 className="font-display text-5xl md:text-8xl max-w-3xl leading-none">
                            NOT JUST COFFEE. <br />
                            <span className="text-gold-500 italic">POETRY</span> IN A CUP.
                        </h3>
                        <p className="text-zinc-500 max-w-xl text-lg">
                            Each roast is a testament to our dedication to the craft. Sourced from single-origin farms and roasted in the heart of Auckland.
                        </p>
                        <Button variant="outline" className="text-white border-white/20 hover:border-gold-500 hover:bg-gold-500 transition-all">
                            MEMBERSHIP DETAILS
                        </Button>
                    </div>

                </main>
            </div>
        </div>
    );
}
